___CSS_LOADER_EXPORT___.push([module.id, `:root{
    --primary-color:rgb(80, 126, 185);
    --primary-color-darker:rgb(30, 58, 95);
}
*{
    box-sizing: border-box;
    outline: 0;
   
}
body{
    margin: 0;
    padding: 0;
    background: var(--primary-color);
    font-family: 'Open sans', sans-serif;
}

.container {
    max-width: 640px;
    margin: 50px auto;
    background: #fff;
    padding: 20px;
    border-radius: 10px;
   
}
form input, form label, form button {
    display: block;
    width: 100%;
    margin-bottom: 10px;
}
form input{
    font-size: 24px;
    height: 50px;
    padding: 0 20px;
}

form input:focus{
    outline: var(--primary-color) solid 1px;
}

form button {
    border: none;
    background-color: var(--primary-color);
    color: #fff;
    font-size: 18px;
    font-weight: 700;
    height: 50px;
    cursor: pointer;
    margin-top: 30px;
}   

form button:hover{
    background: var(--primary-color-darker);
}
form button:active{
    background: var(--primary-color);
